import React from 'react';
import Markdown from 'react-markdown';

const markdown = 
` 
# Hello world!
* This is where you can create a cheatsheet for your presentation.
* Inlcude any key information that you want residents to be able to quickly reference.
* You can include cases or consider adding links to parts of the presentation.

`

function Cheatsheet() {
    return (
        <div className="container">
            <Markdown>{markdown}</Markdown>
        </div>
        
    );
}

export default Cheatsheet;
